/* General Style */
body {
  margin: 0;
  line-height: 1.5;
  font-size: 16px;
  font-family: "Montserrat", sans-serif;
  font-weight: 400;
}
* {
  margin: 0;
  padding: 0;
  outline: none;
  text-decoration: none;
  box-sizing: border-box;
}
::before,
::after {
  box-sizing: border-box;
}
.section {
  min-height: 100vh;
  display: block;
  padding: 0px 30px;
  left: 270px;
  top: 0;
  right: 0;
  z-index: 5 !important;
  overflow-x: hidden;
  overflow-y: auto;
  /* opacity: 1; */
  transition: all 0.3s ease;
}
.main-content {
  padding-left: 270px;
  overflow-y: scroll;
}
.container {
  /* max-width: 1200px; */
  margin: auto;
  width: 100%;
  /* background-color: red; */
}
.shadow-dark {
  box-shadow: 0 0 20px rgba(48, 46, 77, 0.15);
  z-index: 1000;
}
/* Aside */
.aside {
  width: 270px;
  /* background-color: #fdf9ff; */
  position: fixed;
  height: 100%;
  left: 0;
  top: 0;
  z-index: 10;
  /* border-right: 1px solid #e8dfec; */
  padding: 30px;
  transition: all 0.3s ease;
}

.aside .logo {
  padding: 30px 0px;
}
.aside .logo a {
  font-size: 30px;
  font-family: "Rubik", sans-serif;
  /* color: #302e4d; */
  font-weight: 700;
  text-decoration: none;
  display: inline-block;
  padding: 0 10px;
  line-height: 50px;
  position: relative;
}
.aside .logo a::before {
  content: "";
  height: 20px;
  width: 20px;
  position: absolute;
  top: 0;
  left: 0;
}
.aside .logo a::after {
  content: "";
  height: 20px;
  width: 20px;
  position: absolute;
  bottom: 0;
  right: 0;
}
.aside .nav-toggle {
  height: 40px;
  width: 45px;
  border: 1px solid #d4d3e3;
  cursor: pointer;
  position: fixed;
  left: 300px;
  top: 20px;
  z-index: 11000;
  border-radius: 5px;
  background-color: #fdf9ff;
  display: flex;
  display: none;
  align-items: center;
  justify-content: center;
  transition: all 0.3s ease;
}
.aside .nav-toggle span {
  height: 2px;
  width: 18px;
  display: inline-block;
  position: relative;
}
.aside .nav-toggle.open span {
  background-color: transparent;
}
.aside .nav-toggle span::before {
  content: "";
  height: 2px;
  width: 18px;
  position: absolute;
  top: -6px;
  left: 0;
}
.aside .nav-toggle.open span::before {
  transform: rotate(45deg);
  top: 0px;
}
.aside .nav-toggle span::after {
  content: "";
  height: 2px;
  width: 18px;
  position: absolute;
  top: 6px;
  left: 0;
}
.aside .nav-toggle.open span::after {
  transform: rotate(-45deg);
  top: 0px;
}
.aside .nav-menu-div {
  margin: 80px 0px;
  list-style: none;
  margin: 40px 0;
  padding: 0;
}
.aside .nav-menu-div li {
  display: block;
}
.aside .nav-menu-div li a {
  font-size: 16px;
  font-weight: 600;
  color: #302e4d;
  text-decoration: none;
  line-height: 55px;
  display: block;
  /* border-bottom: 1px solid #e8dfec; */
  transition: all 0.3s ease;
  text-transform: capitalize;
}
.aside .nav-menu-div li a:hover {
  padding-left: 15px;
  font-weight: 700;
}

.aside .copyright-text {
  font-size: 13px;
  color: #7d7d7d;
}

/* Home */
.home {
