.content-contact .containerContact .contactForm .formBox .inputBox input:focus~span,
.content-contact .containerContact .contactForm .formBox .inputBox textarea:focus~span,
.content-contact .containerContact .contactForm .formBox .inputBox input:valid~span,
.content-contact .containerContact .contactForm .formBox .inputBox textarea:valid~span{
  transform: translateY(-25px);
  font-size: 12px;
  font-weight: 500;
  letter-spacing: 1px;
  color: #ec1839;
}
.content-contact .containerContact .contactForm .formBox .inputBox input[type="submit"]{
  position: relative;
  cursor: pointer;
  /* background: #0f3959; */
  color: #fff;
  border: none;
  max-width: 150px;
  padding: 12px;
}
.content-contact .containerContact .contactForm .formBox .inputBox input[type="submit"]:hover{
  background: #ec1839;
}

/* SpacecChar */
.spaceChar{
  position: absolute;
  top: 20%;
  right: 0;
  color: #ec1839;
  cursor: pointer;
}
.spaceCharMessage{
  position: absolute;
  top: 0%;
  right: 0;
  color: #ec1839;
  cursor: pointer;
}
.content-contact .containerContact .contactForm .formBox .inputBox input:focus~.spaceChar,
.content-contact .containerContact .contactForm .formBox .inputBox textarea:focus~.spaceCharMessage{
  display: none;
  transition: all 0.3s ease;
}

.spaceChar:hover::before,
.spaceCharMessage:hover::before{
  content: 'Cannot be left blank';
  position: absolute;
  top: 0;
  left: -150px;
  width: 150px;
  height: auto;
  /* background: #eeeedd; */
  z-index: 2;
}

.displayNone{
  display: none;
}
/* Responsive */
@media only screen and (max-width: 950px){
  .content-contact .containerContact{
    display: block;
    width: 100%;
  }
  .content-contact .containerContact .contactInfo{
    position: relative;
    width: 100%;
    height: 320px;
    background: #0f3959;
    z-index: 1; 
    padding: 20px;
    display: flex;
    justify-content: space-between;
    box-shadow: 0 20px 20px rgba(0, 0, 0,0.2);
  }
  .content-contact .containerContact .contactForm{
    background: #fff;
    display: block !important;
    width: 100%;
   margin: 50px 0;
   padding: 70px 20px;
  }
  .content-contact .containerContact .contactForm .formBox .inputBox.w50{
    width: 100%;
  }
  .spaceChar:hover::before,
.spaceCharMessage:hover::before{
  content: 'Cannot be left blank';
  position: absolute;
  top: 0;
  left: -150px;
  width: 150px;
  height: auto;
  background: #eeeedd;
  z-index: 2;
}
}
